#contact {
  color: #fff;
  width: 80vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-content ul,
.contact-header {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-header {
  font-size: 2em;
}
.contact-content ul li {
  margin: 1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact-content ul li img {
  width: 2em;
  height: 2em;
}
.icon {
  padding: 10px;
  border-radius: 50%;
  border: 1px solid #fff;
  transition: all 0.5s ease;
}
.icon:hover {
  box-shadow: inset 1px 0 7px 1px #fff;
}
.icon:active {
  box-shadow: 1px 0 7px 1px #fff;
}
.icon-text {
  opacity: 0;
  transition: all 0.5s ease;
  margin: 5px;
}
@media only screen and (max-width: 750px) {
  #contact {
    width: 100vw;
  }
  .contact-header {
    font-size: 1.5em;
  }
}
@media only screen and (max-width: 420px) {
  #contact {
    width: 100vw;
  }
  .contact-header {
    font-size: 1.2em;
  }
  .contact-content ul li {
    margin: 0.5em;
  }
}
